import React from "react"
import PropTypes from "prop-types"

export default class BaseLayout extends React.Component {

    static propTypes = {
        errSelectors: PropTypes.object.isRequired,
        errActions: PropTypes.object.isRequired,
        specSelectors: PropTypes.object.isRequired,
        oas3Selectors: PropTypes.object.isRequired,
        oas3Actions: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        layoutActions: PropTypes.object.isRequired,
        layoutSelectors: PropTypes.object.isRequired
    }

    showOverview = () => {
        this.props.layoutActions.expand('overview');
    }

    componentDidMount() {
        this.showOverview();
    }

    render() {
        let {specSelectors, getComponent, layoutSelectors} = this.props

        let SvgAssets = getComponent("SvgAssets")
        let InfoContainer = getComponent("InfoContainer", true)
        let VersionPragmaFilter = getComponent("VersionPragmaFilter")
        let Operations = getComponent("operations", true)
        let Models = getComponent("Models", true)
        let Row = getComponent("Row")
        let Col = getComponent("Col")
        let ServersContainer = getComponent("ServersContainer", true)
        let Errors = getComponent("errors", true)

        const SchemesContainer = getComponent("SchemesContainer", true)
        const FilterContainer = getComponent("FilterContainer", true)
        let isSwagger2 = specSelectors.isSwagger2()
        let isOAS3 = specSelectors.isOAS3()

        const isSpecEmpty = !specSelectors.specStr()

        if(isSpecEmpty) {
            let loadingMessage
            let isLoading = specSelectors.loadingStatus() === "loading"
            if(isLoading) {
                loadingMessage = <div className="loading"></div>
            } else {
                loadingMessage = <h4>No API definition provided.</h4>
            }

            return <div className="swagger-ui">
                <div className="loading-container">
                    {loadingMessage}
                </div>
            </div>
        }

        return (

            <div className='swagger-ui'>
                <SvgAssets />
                <VersionPragmaFilter isSwagger2={isSwagger2} isOAS3={isOAS3} alsoShow={<Errors/>}>
                    <Row className="information-container">
                        <Col mobile={12}>
                            <div className="opblock-tag-section" onClick={this.showOverview}>
                                <h4 className="opblock-tag">Introduction</h4>
                            </div>
                        </Col>
                    </Row>

                    <ServersContainer/>

                    <FilterContainer/>

                    <Row>
                        <Col mobile={12} desktop={12} >
                            <Operations/>
                        </Col>
                    </Row>

                    { layoutSelectors.isExpanded('overview') && <InfoContainer/> }
                </VersionPragmaFilter>
            </div>
        )
    }
}



